import { useSelector } from "react-redux";
import { PlantInfo } from "./PlantInfoMap.js";
const convertID = (plant) => {
	let arrayID = plant.split("");
	// aaa-bb-c-dd-xx
	let type = `${arrayID[0]}${arrayID[1]}${arrayID[2]}`;
	let id = parseInt(`${arrayID[3]}${arrayID[4]}`);
	let img = `${arrayID[5]}`;
	let rarity = `${arrayID[6]}${arrayID[7]}`;
	return { type, id, img, rarity };
};

const calculateRarity = (num) => {
	let rarityType = "",
		color = "",
		rarityNum = 0;
	let rarity = parseInt(num);
	if (rarity >= 0 && rarity <= 59) {
		rarityNum = 0;
		rarityType = "Common";
		color = "#198754";
	} else if (rarity >= 60 && rarity <= 88) {
		rarityNum = 1;
		rarityType = "Uncommon";
		color = "#0d6efd";
	} else if (rarity >= 89 && rarity <= 98) {
		rarityNum = 2;
		rarityType = "Rare";
		color = "#dc3545";
	} else if (rarity === 99) {
		rarityNum = 3;
		rarityType = "Mythic";
		color = "#6610f2";
	}
	return { rarityNum, rarityType, color };
};

const searchPlantId = (id) => {
	return PlantInfo.filter((plant) => {
		// eslint-disable-next-line eqeqeq
		return plant.id == id;
	})[0];
};

const calculateLE = (baseLE, rarityNum, rarity, step) => {
	let lowestRarity;
	// eslint-disable-next-line default-case
	switch (rarityNum) {
		case 0:
			lowestRarity = 0;
			break;
		case 1:
			lowestRarity = 60;
			break;
		case 2:
			lowestRarity = 89;
			break;
		case 3:
			lowestRarity = 99;
			break;
	}
	return baseLE[rarityNum] + lowestRarity + (rarity - lowestRarity) * step;
};

const valueCal = (rarity) => {
	// eslint-disable-next-line eqeqeq
	if (rarity == 59 || rarity == 88 || rarity == 98) {
		return "(Most value)";
	}
};

const Output = (props) => {
	const plant = useSelector((state) => state.plant);
	let { id, img, rarity } = convertID(plant);
	let { rarityNum, rarityType, color } = calculateRarity(rarity);
	let info = searchPlantId(id);
	let LE = calculateLE(info.baseLE, rarityNum, rarity, info.step);
	let tax = 95 / 100;
	let LEperHour = (LE / info.hour) * tax;
	let LEperDay = LEperHour * 24 * tax;
	console.log(props.price);
	return plant === "" ? (
		<>
			<p className="text-center h3 mt-4">Wrong input</p>
		</>
	) : (
		<div className="d-flex m-auto mt-4 justify-content-center">
			<Img id={id} imgType={img} />
			<div className="ms-3">
				<p>Type: {info.type}</p>
				<p>
					Rarity: <span style={{ color: `${color}` }}>{rarityType}</span>
				</p>
				<p>Element: {info.element}</p>
				<p>
					LE: {LE}/{info.hour} hours{" "}
					<span style={{ color: `${color}` }}>{valueCal(rarity)}</span>
				</p>
				<p>
					LE per hour: {props.fixFunc(LEperHour, 1)} -{" "}
					{props.fixFunc(LEperHour / 150, 1)} PVU (~
					{props.fixFunc((props.price * LEperHour) / 150, 2)}
					$)
				</p>
				<p>
					LE per day: {props.fixFunc(LEperDay, 1)} -{" "}
					{props.fixFunc(LEperDay / 150, 1)} PVU (~
					{props.fixFunc((props.price * LEperDay) / 150, 2)}$)
				</p>
				<p>(include 5% tax)</p>
			</div>
		</div>
	);
};

const Img = (props) => {
	return (
		<img
			className="w-25 h-25 me-4"
			src={
				process.env.PUBLIC_URL + `/assets/img/${props.id}_${props.imgType}.png`
			}
			alt={props.id}
		/>
	);
};
export default Output;
